.contact-links-container ul {
  list-style-type: none;
  margin-top: 2em;
  width: 70%;
  margin-right: auto;
  margin-left: auto;
}
.contact-links-container ul li {
  display: inline-block;
  padding: 15px;
}
.contact-links-container .icon {
  color: #999;
}
.contact-links-container .resume-title {
  color: #af6c57;
}

.contact-links-container .icon:hover {
  color: #f9f9f9;
  cursor: pointer;
}
.ea-footer {
  font-size: 18px;
  padding-top: 10px;
  color: #999;
}
.react-icon-footer {
  color: cyan;
  margin-left: 8px;
}
