body {
  margin: 0px;
  padding: 0px;
}

.navbar {
  position: sticky;
  top: 0;
  z-index: 9999;
  width: 100%;
}

.navbar-container ul {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: #1b1c1d;
}

.navbar-container ul li {
  float: left;
  font-family: "arial";
  font-size: 15px;
  padding: 14px 14px;
  margin-left: 10px;
  margin-top: 10px;
}

.navbar-container a {
  display: block;
  color: rgb(255, 254, 254);
  text-align: center;

  text-decoration: none;
}

.navbar-container li a:hover {
  color: rgb(177, 174, 174);
  cursor: pointer;
}