.container {
  width: 80%;
  margin-left: auto;
  margin-right: auto;
}
.subContainer {
  display: flex;
}
.projectHeading-hr {
  width: 90%;
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}
.card-container {
  width: 290px;
  margin-top: 30px;
  margin-left: auto;
  margin-right: auto;
  flex-direction: row-reverse;
  flex-wrap: wrap;
  border: 1px solid rgba(0, 0, 0, 0.2);
  border-radius: 5px;
  box-shadow: 0 2px 2px 0 rgba(34, 36, 38, 0.2),
    0 3px 5px 0 rgba(34, 36, 38, 0.1);
}
.card-image img {
  width: 100%;
  height: 250px;
  border-bottom: 1px solid rgba(0, 0, 0, 0.2);
}
.card-description h1 {
  font-size: 20px;
  margin-left: 20px;
  color: #222;
  font-weight: 600;
  line-height: 0px;
}
.card-description .date {
  color: rgba(0, 0, 0, 0.4);
  font-weight: 500;
  font-size: 14px;
  margin-left: 20px;
}
.card-description .description {
  font-size: 14px;
  text-align: left;
  color: rgba(0, 0, 0, 0.7);
  margin-left: 20px;
}
.card-description .tech {
  font-size: 14px;
  color: rgba(0, 0, 0, 0.5);
  margin-left: 20px;
}
.card-description .tech-container {
  border-top: 1px solid rgba(0, 0, 0, 0.2);
}

.section-heading {
  width: 90%;
  margin-right: auto;
  margin-left: auto;
  color: #222;
  padding-top: 50px;
  font-weight: 500;
}
@media (max-width: 800px) {
  .subContainer {
    flex-direction: column;
  }
}

.name-github {
  display: flex;
}
.project-name {
  width: 80%;
}

.project-github-icon {
  width: 20%;
}
.project-github-icon .icon {
  color: #222;
  font-size: 22px;
  margin-left: 15px;
}
.project-github-icon .icon:hover {
  color: #555;
}
