.homeContainer {
  min-height: 670px;
  background-color: #1b1c1d;
  display: flex;
  justify-content: center;
  position: relative;
  width: 100%;
}
.myName {
  text-align: center;
  color: #fff;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  font-size: 30px;
  margin-top: 60px;
}
.myName p {
  font-size: 18px;
}
.profile-img {
  background-image: url("https://media-exp1.licdn.com/dms/image/C5603AQHJCKExDf80uw/profile-displayphoto-shrink_400_400/0/1621023397067?e=1626912000&v=beta&t=EeKxRloUS2Nnuvz22rnMav4gxYjM-0WWN2kWoYw082s");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: bottom center;
  width: 150px;
  height: 150px;
  border-radius: 50%;
  margin: auto;
  margin-top: 40px;
}
.about-me-home {
  margin-top: 150px;
}
.about-me-home p {
  color: rgb(224, 222, 222);
  width: 600px;
  border: 1px solid white;
  margin: auto;
  font-size: 20px;
  padding: 5px;
  text-align: center;
  line-height: 25px;
}
.about-me-home span {
  color: #999;
  font-size: 22px;
}
.rc {
  width: 50%;
  margin-right: auto;
  margin-left: auto;
  border: 1px solid #333;
  text-align: center;
  font-size: 22px;
  height: 40px;
  border-radius: 7px;
  cursor: pointer;
}
.rc:hover {
  background-color: #333;
}
.rc a {
  text-decoration: none;
  color: #fff;
}
