.experience-main {
  width: 90%;
  margin-right: auto;
  margin-left: auto;
}
.experienceContainer img {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  border: 1px solid rgb(0, 0, 0, 0.1);
  position: absolute;
}
.experienceContainer .jobTitle {
  margin-left: 75px;
  margin-top: 20px;
  color: #222;
  font-size: 22px;
  line-height: 10px;
  font-weight: 500;
}
.experienceContainer .company {
  margin-left: 75px;
  margin-top: 0px;
  font-size: 20px;
  color: rgb(0, 0, 0, 0.6);
  font-weight: 500;
  line-height: 15px;
}
.experienceContainer .date {
  margin-left: 75px;
  font-size: 16px;
  color: rgb(0, 0, 0, 0.5);
  line-height: 5px;
  font-weight: 500;
}
.experienceContainer ul {
  margin-left: 90px;
  font-size: 17px;
  color: #222;
  padding: 2px;
}
.experienceContainer ul span {
  font-weight: 600;
}
#headerTitle {
  width: 90%;
  margin-right: auto;
  margin-left: auto;
  color: #222;
  margin-top: 50px;
  font-weight: 500;
}
.experienceContainer ul li {
  padding: 3px;
}
