html {
  scroll-behavior: smooth;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
}
* {
  box-sizing: border-box;
}
.aboutContainer {
  background-color: #fff;
  width: 90%;
  margin-top: 6%;
  margin-left: auto;
  margin-right: auto;
}
.aboutMeFlexContainer {
  display: flex;
  flex-wrap: wrap;
}
.home-desc {
  width: 600px;
  padding: 10px;
  flex: 50%;
}
.home-skills {
  width: 600px;
  font-family: "arial";
  color: #222;
  padding: 10px;
  flex: 50%;
  margin-top: 10px;
  box-shadow: 0 2px 2px 0 rgba(34, 36, 38, 0.2),
    0 3px 5px 0 rgba(34, 36, 38, 0.1);
}

.section-header {
  font-size: 2rem;
  color: #222;
  font-weight: 500;
}
.headings-hr {
  width: 100%;
  border-top: 0.5px solid rgba(34, 36, 38, 0.2);
}

.home-desc p {
  font-size: 16px;
  color: #222;
  text-align: left;
}
.home-desc span {
  color: #444;
  font-weight: 650;
}
.home-desc ul {
  list-style-type: circle;
}
.home-skills {
  box-sizing: border-box;
  border: 1px solid rgba(34, 36, 38, 0.2);
  border-radius: 5px;
  display: flex;
}
