.frontEnd-skills-main,
.backEnd-skills-main {
  width: 50%;
  height: 100%;
}
.frontEnd-skills-main h1,
.backEnd-skills-main h1 {
  font-size: 16px;
  margin-left: 30px;
}
.frontEnd-list,
.backEnd-list {
  margin-right: auto;
  margin-left: auto;
}
.frontEnd-list ul,
.backEnd-list ul {
  list-style-type: none;
}
.frontEnd-list ul li,
.backEnd-list ul li {
  padding-top: 30px;
  color: #111;
  font-size: 18px;
}
.frontEnd-list .skillIcon,
.backEnd-list .skillIcon {
  font-size: 28px;
  margin-right: 10px;
  font-weight: bold;
}
.react {
  color: #0d93b4;
}
.html {
  color: #e34c26;
}
.js {
  color: #f0d31a;
}
.java {
  color: red;
}
.python {
  color: #306998;
}
.SQL {
  color: #f29111;
}
.c {
  color: #283593;
}
.git {
  color: #f34f29;
}
@media (max-width: 800px) {
  .home-desc,
  .home-skills {
    flex: 100%;
  }
}
.hr-fullSize {
  width: 100%;
  border-top: 1px solid rgba(34, 36, 38, 0.2);
  margin-top: 70px;
}
.video-container {
  margin-left: auto;
  margin-right: auto;
  width: 75%;
  text-align: center;
  margin-top: 50px;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Oxygen,
    Ubuntu, Cantarell, "Open Sans", "Helvetica Neue", sans-serif;
  color: rgba(0, 0, 0, 0.9);
}
.video-container h2 {
  font-size: 20px;
  color: rgba(0, 0, 0, 0.7);
}
.video-container span {
  color: #222;
}
